	export default {
		data() {
			return {
				imgUrl: "",
				shopInfo: {}, //商品详情信息
			}
		},
		onLoad(option){
			this.imgUrl = this.$store.state.imgUrl;
			this.api({
				method: "get",
				url: "item/info",
				data: {
					id: option.id
				}
			}).then(res=> {
				console.log(res);
				this.shopInfo = res.data.date;
				this.shopInfo.distance = option.distance;
			})
		},
		methods: {
			buy() {
				this.api({
					method: "get",
					url: "Api/add_order",
					data: {
						itemid: this.shopInfo.id,
						shop_id: this.shopInfo.shop_id
					}
				}).then(res=> {
					console.log(res);
					// wx.requestPayment()
					uni.requestPayment({
					  timeStamp: res.data.date.timeStamp,
					  nonceStr: res.data.date.nonceStr,
					  package: res.data.date.package,
					  signType: res.data.date.signType,
					  paySign: res.data.date.paySign,
					  success: (ok)=> { 
						  console.log(ok);
						  if(ok.errMsg == 'requestPayment:ok') {
							uni.showModal({
								title: '提示',
								content: '购买成功',
								showCancel: false,
								cancelText: '',
								confirmText: '确定',
								success: res => {
									uni.switchTab({
										url: '../userTab/home'
									})
								},
								fail: () => {},
								complete: () => {}
							});
						  }
					  },
					  fail: (err)=> { 
						  console.log(err);
					  }
					})
				})
			}
		}
 	}
